import React from 'react';
import { motion } from 'framer-motion';
import { Card } from '../Card';
import { cn } from '../../utils';

/**
 * SubjectCard Component
 * Card for displaying subject/lesson selection
 */
export const SubjectCard = ({
  icon,
  title,
  description,
  onClick,
  delay = 0,
  className = '',
  stats = [],
  statsVariant = 'light',
  titleClassName = 'text-xl font-semibold text-gray-800 group-hover:text-primary-600 transition-colors duration-300',
  descriptionClassName = 'text-sm text-gray-600 group-hover:text-gray-700 transition-colors duration-300',
  iconClassName = 'text-4xl mb-2 transform group-hover:scale-110 transition-transform duration-300',
  ...props
}) => {
  const isDarkStats = statsVariant === 'dark';

  const statCardClasses = cn(
    'rounded-xl px-2.5 py-1.5 border text-xs font-medium',
    isDarkStats
      ? 'bg-white/10 border-white/20 text-white'
      : 'bg-gray-50 border-gray-100 text-gray-800'
  );

  return (
    <motion.div
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ delay }}
      {...props}
    >
      <Card
        onClick={onClick}
        className={cn('cursor-pointer text-center group relative overflow-hidden', className)}
      >
        {/* Hover gradient effect */}
        <div className="absolute inset-0 bg-gradient-to-br from-primary-500/0 to-secondary-500/0 group-hover:from-primary-500/10 group-hover:to-secondary-500/10 transition-all duration-300" />
        
        <div className="relative z-10 flex flex-col items-start gap-2 text-left">
          {icon && (
            <motion.div
              className={iconClassName}
              whileHover={{ rotate: [0, -10, 10, -10, 0] }}
              transition={{ duration: 0.5 }}
            >
              {icon}
            </motion.div>
          )}
          <h3 className={titleClassName}>
            {title}
          </h3>
          {description && (
            <p className={descriptionClassName}>
              {description}
            </p>
          )}

          {Array.isArray(stats) && stats.length > 0 && (
            <div className="mt-3 flex flex-wrap gap-2 w-full">
              {stats.map((stat, index) => (
                <div key={`${stat.label}-${index}`} className={statCardClasses}>
                  <span className={isDarkStats ? 'text-white/70 mr-1' : 'text-gray-500 mr-1'}>
                    {stat.label}:
                  </span>
                  <span className="font-semibold">{stat.value ?? '0'}</span>
                </div>
              ))}
            </div>
          )}
        </div>
      </Card>
    </motion.div>
  );
};

